import React, {useState} from 'react'
import '../css/WorkComfirmSession.css'
import DatePicker ,{ registerLocale } from "react-datepicker";  // 
import ko from 'date-fns/locale/ko'; // 
import "react-datepicker/dist/react-datepicker.css";

function WorkComfirmSession() {
    // 
  const colums = ["", "", "", ""];
  // 
  const workerList =[
    {
        workerName : '',
        workerNum : 20180802,
        workerType : '',
        workerTime : '2022. 02. 26 - 2022. 06. 24'
    },
    {
        workerName : '',
        workerNum : 20190323,
        workerType : '',
        workerTime : '2022. 02. 26 - 2022. 06. 24'
    },
    {
        workerName : "",
        workerNum : 20190423,
        workerType : "",
        workerTime : '2022. 02. 26 - 2022. 06. 24'
    },
    {
        workerName : "",
        workerNum : 20190606,
        workerType : "",
        workerTime : '2022. 02. 26 - 2022. 06. 24'
    },
    {
        workerName : "",
        workerNum : 20190813,
        workerType : "",
        workerTime : '2022. 02. 26 - 2022. 06. 24'
    },
    {
        workerName : '',
        workerNum : 20201122,
        workerType : "",
        workerTime : '2022. 02. 26 - 2022. 06. 24'
    },
    {
        workerName : '',
        workerNum : 20210205,
        workerType : '',
        workerTime : '2022. 02. 26 - 2022. 06. 24'
    }
  ];

  const [workStart, setWorkStart] = useState(new Date());
  const [workEnd, setWorkEnd] = useState(new Date());
  registerLocale("ko", ko);

  function workerComfirmTable() {
    return (
      <table className='comfirmList'>
        <thead>
          <tr>
            <input type="checkbox"/>
            {colums.map((col) => (
              <th key={col}>{col}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {workerList.map(({workerName, workerNum, workerType, workerTime}) => (
            <tr key={workerNum}>
              <input type="checkbox"/>
              <td className='table_workerList'>{workerName}</td>
              <td className='table_workerList'>{workerNum}</td>
              <td className='grade_workerList'>{workerType}</td>
              <td className='major_workerListtime'>{workerTime}</td>
            </tr>
          ))}
        </tbody>
      </table>
    );
  }

  const workComfirmDelete = () => {
    alert(".");
  }

  const workComfirmPlus = () => {
    alert(".");
  }

  const totalTime = 12;
  const totalAmount = 109920;

  return (
    <div className='WorkComfirmSession'>
        <div className='WorkComfirmMain'>
            <div className='workerSearch'>
                <input className='search_worker' type='text' placeholder=''></input>
                <button className='search_btn'></button>
            </div>
            <div className='workerComfirmList'>
                <div className='comfirmList'>
                    {workerComfirmTable()}
                </div>
                <button className='comfirmDelete_btn' onClick={() => workComfirmDelete}></button>
            </div>
            <div className='workerComfirmPlus'>
                <select className='workerType'>
                    <option value={1}></option>
                    <option value={2}></option>
                    <option value={3}>  </option>
                </select>
                <DatePicker
                    className='workStartDatepick'
                    selected={workStart}
                    dateFormat="yyyy MM dd"
                    onChange={date => setWorkStart(date)}
                    locale="ko"
                    width="300px"
                /> 
                <DatePicker
                    className='workEndDatepick'
                    selected={workEnd}
                    dateFormat="yyyy MM dd"
                    onChange={date => setWorkEnd(date)}
                    locale="ko"
                    width="300px"
                />
                <button className='workcomfirmplus_btn' onClick={() => workComfirmPlus()}></button>
            </div>
            <div className='workerComfirmTotal'>
                <div className='workerTotal'>   {totalTime} </div>
                <div className='workerTotal'>      {totalAmount} </div>
            </div>
        </div>
    </div>
  )
}

export default WorkComfirmSession